import {
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
  Stack,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
} from "@mui/material";
import { useEffect, useRef, useState } from "react";
import { useLocation } from "react-router-dom";

import {
  ExerciseData,
  SeriesData,
  TrainingSessionDataFromApi,
} from "../../interfaces/trainingsInterf";
import { updateExercises } from "../../store/features/trainings/trainingsActionCreators";
import { useAppDispatch } from "../../store/hooks/hooks";
import AddIcon from "@mui/icons-material/Add";
import CloseIcon from "@mui/icons-material/Close";

import { MuiTextFieldPropsError } from "../auth/AuthSide";
import { nanoid } from "@reduxjs/toolkit";
import DeleteIcon from "@mui/icons-material/Delete";
import { ExerciseDialogStateType } from "./Training";

import { useSelectActionStatusAndError } from "../../store/features/trainings/trainingsSlice";
import { useSetSnackbarContext } from "../../components/snackbar-provider/SnackbarProvider";

import {
  useForm,
  Controller,
  SubmitHandler,
  useFieldArray,
  UseFormSetValue,
  FieldPath,
} from "react-hook-form";

const maxCommentSigns = 255;
const maxExerciseNameSigns = 100;

type Series = {
  [Property in keyof SeriesData]: SeriesData[Property] | "";
} & { key: string };

interface FormDataType {
  _id?: string;
  name: string;
  comment: string;
  series: Series[];
}

const acceptOnlyDigits = (
  e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>,
  setValue: UseFormSetValue<FormDataType>,
  filedName: FieldPath<FormDataType>,
  allowDash = false
) => {
  if (e.target.value.match(/^\d*$/) || (allowDash && e.target.value === "-")) {
    setValue(filedName, e.target.value, { shouldValidate: true });
  }
};

const AddOrEditTrainingExerciseDialog = ({
  exerciseDialogState,
  setExerciseDialogState,
  trainingSession,
}: {
  exerciseDialogState: ExerciseDialogStateType;
  setExerciseDialogState: React.Dispatch<
    React.SetStateAction<ExerciseDialogStateType>
  >;
  trainingSession: TrainingSessionDataFromApi | undefined;
}) => {
  const setSnackbar = useSetSnackbarContext();

  const [actionStatus] = useSelectActionStatusAndError();
  const dispatch = useAppDispatch();
  const location = useLocation();
  const requestInProgress = useRef(false);

  const [indexForAutofocus, setIndexAutofocus] = useState<number | null>(null);

  const elementToAutofocus = useRef<HTMLInputElement | null>(null);
  useEffect(() => {
    if (elementToAutofocus.current) {
      elementToAutofocus?.current?.focus();
      elementToAutofocus.current = null;
      setIndexAutofocus(null);
    }
  }, [elementToAutofocus, indexForAutofocus]);

  const {
    handleSubmit,
    setValue,
    control,
    formState: { isValid, isSubmitting },
  } = useForm<FormDataType>({
    defaultValues: exerciseDialogState.initialValues
      ? {
          _id: exerciseDialogState.initialValues._id,
          name: exerciseDialogState.initialValues.name,
          comment: exerciseDialogState.initialValues.comment,
          series: exerciseDialogState.initialValues.series.reduce<Series[]>(
            (previousValue, currentValue) =>
              previousValue.concat([
                {
                  key: nanoid(),
                  // key: currentValue._id,
                  load: currentValue.load,
                  quantity: currentValue.quantity,
                },
              ]),
            []
          ),
        }
      : {
          name: "",
          comment: "",
          series: [] as Series[],
        },
    mode: "onTouched",
    criteriaMode: "firstError",
    shouldUnregister: true,
  });

  const { fields, append, remove, insert } = useFieldArray({
    control,
    name: "series",
    shouldUnregister: true,
  });

  const onSubmit: SubmitHandler<FormDataType> = async (values) => {
    // protection against submitting form twice
    if (!requestInProgress.current) {
      requestInProgress.current = true;

      try {
        const exercises_shallowCopyForMutation: ExerciseData[] = [
          ...(trainingSession?.exercises ?? []),
        ];
        exercises_shallowCopyForMutation.splice(
          (exerciseDialogState.index ?? trainingSession?.exercises.length) || 0,
          exerciseDialogState.initialValues ? 1 : 0,
          {
            ...(values._id ? { _id: values._id } : undefined),
            name: values.name ?? "",
            comment: values.comment ?? "",
            series:
              values.series.reduce<SeriesData[]>(
                (previousValue, currentValue) =>
                  previousValue.concat([
                    {
                      load:
                        currentValue.load === "-" || !currentValue.load
                          ? 0
                          : currentValue.load,
                      quantity: !currentValue.quantity
                        ? 0
                        : currentValue.quantity,
                    },
                  ]),
                []
              ) ?? [],
          }
        );

        await addUpdateExercise(exercises_shallowCopyForMutation);
      } finally {
        requestInProgress.current = false;
      }
    }
  };

  const closeDialog = () => {
    setExerciseDialogState({ open: false });
  };

  const addUpdateExercise = async (exercises: ExerciseData[]) => {
    try {
      await dispatch(
        updateExercises({
          _id: trainingSession?._id ? trainingSession._id : "",
          exercises: exercises,
          urlQuery: location.search,
        })
      ).unwrap();
      setSnackbar(
        exerciseDialogState.initialValues
          ? "Exercise Updated"
          : "Exercise Added",
        "success"
      );
    } catch (error) {
      console.error("Failed to add exercise: ", error);
    } finally {
      closeDialog();
    }
  };

  return (
    <Dialog
      open={exerciseDialogState.open}
      onClose={closeDialog}
      scroll="paper"
      aria-labelledby="add-edit-exercise-dialog-title"
      sx={{ margin: "0rem" }}
    >
      <DialogTitle id="add-edit-exercise-dialog-title">
        {exerciseDialogState.initialValues ? "Update Exercise" : "Add Exercise"}
        <IconButton
          aria-label="close"
          onClick={closeDialog}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <form noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
        <DialogContent
          dividers={true}
          sx={{ opacity: actionStatus === "PROCESSING" ? 0.5 : 1 }}
        >
          <Stack spacing={0} direction="column" alignItems="center">
            <Controller
              render={({ field, fieldState, formState }) => (
                <TextField
                  id="name"
                  margin="dense"
                  fullWidth
                  label="Exercise name"
                  variant="filled"
                  inputProps={{ maxLength: maxExerciseNameSigns }}
                  autoFocus
                  {...MuiTextFieldPropsError(field, fieldState)}
                />
              )}
              name="name"
              control={control}
              rules={{
                required: "Required",
                maxLength: {
                  value: maxExerciseNameSigns,
                  message: `Max length is ${maxExerciseNameSigns} characters`,
                },
              }}
            />

            <Controller
              render={({ field, fieldState, formState }) => (
                <TextField
                  id="comment"
                  margin="dense"
                  fullWidth
                  label="Exercise comment"
                  multiline
                  variant="filled"
                  inputProps={{ maxLength: maxCommentSigns }}
                  {...MuiTextFieldPropsError(field, fieldState)}
                />
              )}
              name="comment"
              control={control}
              rules={{
                maxLength: {
                  value: maxCommentSigns,
                  message: `Max length is ${maxCommentSigns} characters`,
                },
              }}
            />

            {fields && fields.length > 0 ? (
              <Stack>
                <TableContainer
                  component="table"
                  sx={{
                    "& .MuiTableCell-root": {
                      padding: "0 0.25rem",
                      textAlign: "center",
                    },
                    "& .MuiTableCell-body": {
                      padding: "0.4rem 0.25rem",
                    },
                  }}
                >
                  <TableHead>
                    <TableRow>
                      <TableCell></TableCell>
                      <TableCell>Nr</TableCell>
                      <TableCell>Load&nbsp;(kg)</TableCell>
                      <TableCell>Quantity</TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    {fields.map((field, index) => (
                      <TableRow key={field.id}>
                        <TableCell>
                          <IconButton
                            aria-label="delete series"
                            onClick={() => remove(index)}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </TableCell>
                        <TableCell>
                          <Typography variant="h5"> {index + 1}</Typography>
                        </TableCell>
                        <TableCell>
                          <Controller
                            render={({ field, fieldState, formState }) => (
                              <TextField
                                hiddenLabel
                                size="small"
                                required
                                variant="filled"
                                inputProps={{
                                  maxLength: 3,
                                }}
                                sx={{
                                  width: "3.5rem",
                                  transform: "translateY(0.7rem)",
                                  "& .MuiFormHelperText-root": {
                                    ml: "0.1rem",
                                    mt: "0.1rem",
                                  },
                                  "& .MuiFilledInput-input": {
                                    textAlign: "center",
                                  },
                                }}
                                {...MuiTextFieldPropsError(field, fieldState)}
                                inputRef={
                                  indexForAutofocus === index
                                    ? elementToAutofocus
                                    : null
                                }
                                onChange={(e) => {
                                  acceptOnlyDigits(
                                    e,
                                    setValue,
                                    field.name,
                                    true
                                  );
                                }}
                              />
                            )}
                            name={`series.${index}.load` as const}
                            control={control}
                            rules={{
                              validate: {
                                inAllowRange: (value) => {
                                  if (value !== "-") {
                                    const quantity = Number(value);
                                    if (
                                      Number.isNaN(quantity) ||
                                      quantity < 0 ||
                                      quantity > 999
                                    ) {
                                      return "<0,999>";
                                    }
                                  }
                                  return true;
                                },
                              },
                            }}
                          />
                        </TableCell>
                        <TableCell>
                          <Controller
                            render={({ field, fieldState, formState }) => (
                              <TextField
                                hiddenLabel
                                size="small"
                                required
                                variant="filled"
                                inputProps={{
                                  maxLength: 3,
                                }}
                                sx={{
                                  width: "3.5rem",
                                  transform: "translateY(0.7rem)",
                                  "& .MuiFormHelperText-root": {
                                    ml: "0.1rem",
                                    mt: "0.1rem",
                                  },
                                  "& .MuiFilledInput-input": {
                                    textAlign: "center",
                                  },
                                }}
                                {...MuiTextFieldPropsError(field, fieldState)}
                                inputRef={
                                  indexForAutofocus === index
                                    ? elementToAutofocus
                                    : null
                                }
                                onChange={(e) => {
                                  acceptOnlyDigits(
                                    e,
                                    setValue,
                                    field.name,
                                    false
                                  );
                                }}
                              />
                            )}
                            name={`series.${index}.quantity` as const}
                            control={control}
                            rules={{
                              required: "Required",
                              validate: {
                                inAllowRange: (value) => {
                                  const quantity = Number(value);
                                  if (
                                    Number.isNaN(quantity) ||
                                    quantity < 1 ||
                                    quantity > 999
                                  ) {
                                    return "<1,999>";
                                  }
                                  return true;
                                },
                              },
                            }}
                          />
                        </TableCell>
                        <TableCell>
                          <IconButton
                            aria-label="add series"
                            onClick={() => {
                              insert(index, {
                                key: nanoid(),
                                load: "",
                                quantity: "",
                              });

                              setIndexAutofocus(index);
                            }}
                          >
                            <AddIcon />
                          </IconButton>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </TableContainer>
              </Stack>
            ) : undefined}

            <Button
              variant="outlined"
              startIcon={<AddIcon />}
              onClick={() => {
                append({
                  key: nanoid(),
                  load: "",
                  quantity: "",
                });
                setIndexAutofocus(fields.length);
              }}
              sx={{
                mt: fields.length > 0 ? "1.6rem" : "0.4rem",
                mb: "0.8rem",
              }}
            >
              Add series
            </Button>
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button onClick={closeDialog}>Cancel</Button>

          <Button
            type="submit"
            size="large"
            disabled={isSubmitting || !isValid || actionStatus === "PROCESSING"}
          >
            {actionStatus === "PROCESSING" && (
              <CircularProgress size={24} sx={{ mr: "0.5rem" }} />
            )}
            {exerciseDialogState.initialValues ? "Update" : "Add"}
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default AddOrEditTrainingExerciseDialog;
